let SlidersArray = []
function sliderOffers() {
    $(".offers").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.offers[data-slider-id="${index}"] .mySwiper-main`, {
            slidesPerView: 4,
            spaceBetween: 20,
            observer: true,
            observeParents: true,
            grid: {
                rows: 1,
            },
            navigation: {
                nextEl: `.offers[data-slider-id="${index}"] .offers-next`,
                prevEl: `.offers[data-slider-id="${index}"] .offers-prev`,
            },
            breakpoints: {
                320: {
                    slidesPerView: 1,
                },
                577: {
                    slidesPerView: 2,
                    grid: {
                        rows: 2,
                    },
                },
                993: {
                    slidesPerView: 3,
                    grid: {
                        rows: 1,
                    },
                },
                1361: {
                    slidesPerView: 4,
                    grid: {
                        rows: 1,
                    },
                }
            }
        })
    })
}

let SlidersArray2 = []
function sliderOfferCard() {
    $(".offer-card__slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray2[index] = new Swiper(`.offer-card__slider[data-slider-id="${index}"] .swiper`, {
            slidesPerView: 1,
            spaceBetween: 8,
            observer: true,
            observeParents: true,
            navigation: {
                nextEl: `.offer-card__slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.offer-card__slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.offer-card__slider[data-slider-id="${index}"] .swiper-pagination`,
                clickable: true,
            },
        })
    })
}

function gallery() {
    let SlidersArray3 = []

    let sliders = document.querySelectorAll(".gallery")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let gallery = {
            mySwiper: {},
            mySwiper2: {}
        }

        gallery.mySwiper = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper`, {
            slidesPerView: "4",
            spaceBetween: 3,
            freeMode: true,
            watchSlidesProgress: true,
            direction: "vertical",
            breakpoints: {
                320: {
                    spaceBetween: 7,
                    direction: "horizontal",
                },
                577: {
                    spaceBetween: 3,
                    direction: "vertical",
                }
            }
        });
        gallery.mySwiper2 = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper2`, {
            slidesPerView: "1",
            spaceBetween: 10,
            thumbs: {
                swiper:  gallery.mySwiper,
            },
            navigation: {
                nextEl: `.gallery[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.gallery[data-slider-id="${index}"] .swiper-button-prev`,
            },
        });

        SlidersArray3[index] = gallery
    })

}

function sliderPortfolio() {
    new Swiper(".portfolio .mySwiper",{
        slidesPerView: 1,
        spaceBetween: 12,
        navigation: {
            nextEl: ".portfolio .swiper-button-next",
            prevEl: ".portfolio .swiper-button-prev"
        },
    })
}

$(document).ready(function() {
    sliderOffers();
    sliderOfferCard();
    gallery();
    sliderPortfolio();

    // Menu
    $(".menu-item").hover(onIn, onOut);
    if (window.matchMedia("(min-width: 768px)").matches) {
        $(".menu-item .menu-item__show").click(function() {
            if ($(this).parent(".menu-item").hasClass("menu-item--active")) {
                $(this).parent(".menu-item").removeClass("menu-item--active")
            } else {
                $(this).parent(".menu-item").addClass("menu-item--active")
            }
        })
    }

    if (window.matchMedia("(max-width: 767px)").matches) {
        $(".menu-item__show").click((function() {
            $(this).siblings(".sub-menu").slideToggle()
        }));
    }

    $('.burger').click(function () {
        $('.header-mob').addClass('header-mob--active')
        $('body').addClass('fixed-body')
    })

    $('.header-mob__close').click(function () {
        $('.header-mob').removeClass('header-mob--active')
        $('body').removeClass('fixed-body')
    })

    // Select
    $(".custom_select select").niceSelect()

    // Favorite
    $('.favorite').click(function () {
        $(this).toggleClass('favorite--active')
    })

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });



})

function onIn() {
    if (window.innerWidth > 768) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("menu-item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 768) {
        $(this).removeClass("menu-item--active")
    }
}